.services__card {
  text-align: center;
  padding: 1rem;
  transition: 0.3s;
  border-radius: 0.5rem;
  cursor: pointer;
}
.services__card:hover {
  transform: scale(1.01, 1.01);
  background-color: rgb(38, 57, 77);
}
.services__card img {
  margin-bottom: 1.5rem;
}
.services__card h2 {
  margin-bottom: 0.5rem;
}
.services__card p {
  padding: 0 2rem;
}
