.body {
  background-image: url("./images/bg-curvy-desktop.svg");
  background-size: contain;
  background-color: hsl(217, 28%, 15%);
  background-position: bottom;
  background-repeat: no-repeat;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 10rem;
}
.body__imgcontainer {
}
.body__imgcontainer-img {
  width: 100%;
}

.body__typo {
  padding: 0 20%;
  margin-top: 3rem;
  text-align: center;
}
.body__typo-para {
  margin-top: 1.5rem;
}
.body__imgcontainer-img {
  object-fit: cover;
}
.body__typo-span {
  margin-top: 1.5rem;
  padding: 1rem 4rem;
  color: white;
  background-image: linear-gradient(
    to right,
    hsl(176, 68%, 64%),
    hsl(198, 60%, 50%)
  );
  border: none;
  border-radius: 2rem;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  transform: scale(1, 1);
}
.body__typo-span:hover {
  transform: scale(1.1, 1.1);
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}
