.header {
  position: relative;
  font-size: 1rem;
  background-color: hsl(217, 28%, 15%);
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hamburger {
  position: absolute;
  right: 0;
  margin-right: 5%;
  width: 30px;
  height: 30px;
  flex-direction: column;
  justify-content: space-around;
  cursor: pointer;
  z-index: 100;
  display: none;
}
.line {
  background-color: #fff;
  width: 90%;
  border-radius: 3px;
  height: 3px;
  margin: 0 auto;
  transition: all 0.3s ease-in-out;
}

.line::before,
.line::after {
  content: "";
  position: absolute;
  width: 90%;
  display: block;
  height: 3px;
  background-color: white;
  transition: all 0.3s ease-in-out;
}
.line::before {
  transform: translateY(-10px);
}
.line::after {
  transform: translateY(10px);
}
#toggle::before {
  transform: rotate(225deg);
}
#toggle {
  background: transparent;
}
#toggle::after {
  transform: rotate(-225deg);
}

