.header__logo {
  width: 8rem;
  object-fit: contain;
  margin: 0 2rem;
  cursor: pointer;
}
.header__nav ul {
  list-style-type: none;
  display: flex;
}

.mobile ul {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 6rem;
  right: 0;
  left: 0;
  height: 90vh;
  background-color: hsl(216, 53%, 9%);
  z-index: 1000;
  list-style-type: none;
  flex-direction: column;
  transition: all 0.3s;
}

.mobile ul li a {
  text-decoration: none;
  color: rgb(173, 173, 173);
  padding: 0.5rem 1rem;
  border-radius: 5px;
}
.mobile ul li a:hover {
  background-color: hsl(219, 30%, 18%);
}
.mobile ul li {
  margin-top: 2rem;
}

.header__nav ul li a {
  text-decoration: none;
  color: rgb(173, 173, 173);
  margin-right: 2rem;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: all 0.3s;
}

.header__nav ul li a:hover {
  background-color: hsl(219, 30%, 18%);
}

@media only screen and (max-width: 700px) {
  .hamburger {
    display: flex;
  }
  .header__nav ul {
    display: none;
  }
}
