.email {
  position: absolute;
  transform: translateY(-50%);
  z-index: 1;
  margin: 0 15%;
  padding: 2rem 2rem;
  background-color: hsl(217, 28%, 15%);
  text-align: center;
}

.email p,
h1 {
  margin-bottom: 2rem;
}
.email_inputs {
  display: flex;
  gap: 1rem;
  justify-content: space-around;
  align-items: center;
}
.email_input {
  flex: 1;
  padding: 1rem 3rem;
  border-radius: 2rem;
  border: none;
  outline: none;
}

@media only screen and (max-width: 850px) {
  .email {
  }
  .email_inputs {
    flex-direction: column;
  }
  .email_input {
    width: 70%;
  }
}

@media only screen and (max-width: 400px) {
  .email {
    margin: 0 5%;
  }
}

.email_input-btn {
  padding: 1rem 2rem;
  color: white;
  background-image: linear-gradient(
    to right,
    hsl(176, 68%, 64%),
    hsl(198, 60%, 50%)
  );
  border: none;
  border-radius: 2rem;
  cursor: pointer;
  transition: all 0.3s;
}

.email_input-btn:hover {
  transform: scale(1.01, 1.01);
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}
