.productive {
  display: grid;
  margin: 10rem 10%;

  grid-template-columns: 1fr 1fr;
  justify-items: center;
  gap: 5%;
  align-items: center;
}
@media only screen and (max-width: 850px) {
  .productive {
    grid-template-columns: 1fr;
  }
}

.productive img {
  width: 100%;
}

.productive__text h1 {
  margin-bottom: 1rem;
}

.productive__text p {
  margin-bottom: 1rem;
}

.productive__text span {
  font-size: 1rem;
  display: block;
  position: absolute;
  color: hsl(176, 68%, 64%);
  border-bottom: 1px solid hsl(176, 68%, 64%);
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.3s;
}

.arrow-icon {
  border-radius: 50%;
  border: none;
  background-color: hsl(176, 68%, 64%);
  color: black;
}

.productive__text span:hover {
  transform: translateX(1rem) scale(1.1, 1.1);
}
