.test_card {
  background-color: hsl(219, 30%, 18%);
  padding: 1.5rem;
  margin-right: 1rem;
  position: relative;
  cursor: pointer;
  margin-bottom: 1rem;
}

.test_card-flex {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.test_card-p {
  margin-bottom: 1rem;
}
.test_card-img {
  width: 3rem;
  border-radius: 5rem;
}

.test_card-flex-p {
  margin-bottom: 0.3rem;
}
