.services {
  max-width: 1440px;
  margin: 0 10%;
  font-size: 1rem;
  background-color: hsl(218, 28%, 13%);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 10%;
  justify-content: center;
  justify-items: center;
}
@media only screen and (max-width: 850px) {
  .services {
    grid-template-columns: 1fr;
    gap: 5%;
    margin-bottom: 15rem;
  }
}
