.footer {
  position: relative;
  background-color: hsl(216, 53%, 9%);
  padding: 15rem 5% 5% 5%;
}

.footer__logo img {
  width: 9rem;
  object-fit: cover;
  cursor: pointer;
}
.footer__typo {
  display: grid;
  grid-template-columns: 3fr repeat(4, 1fr);
  gap: 5%;
  margin-top: 3rem;
}

@media only screen and (max-width: 850px) {
  .footer__typo {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .col--5 {
    justify-self: center;
  }
}

.icon {
  margin: 0.5rem 0.5rem;
  cursor: pointer;
  padding: 5px;
  border: 1px solid;
  border-radius: 50%;
  transition-duration: 1s;
}

.icon:hover {
  color: hsl(176, 68%, 64%);
}
.col {
  display: flex;
}

.col ul {
  list-style: none;
}
.col ul li a {
  white-space: nowrap;
  text-decoration: none;
  color: rgb(173, 173, 173);
  font-size: 0.9rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  transition: 0.3s ease-in-out;
}

.col ul li a:hover {
  background-color: hsl(219, 30%, 18%);
}
.col ul li {
  margin-top: 1rem;
  display: flex;
  align-items: center;
}
